.notices {
  margin: 15px 0;
  position: relative;
}

.notices p {
  padding: 15px;
  margin: 0;
  box-sizing: border-box;
}
.notices p::before {
  position: absolute;
  box-sizing: border-box;
  top: 5.5px;
  content: url("../../assets/info-circle-solid.svg");
  width: 18px !important;
  height: 18px !important;
  left: 10px;
  font-size: 19px;
}
.notices p::after {
  position: absolute;
  box-sizing: border-box;
  top: 4px;
  color: #fff;
  left: 2rem;
  content: "";
  font-size: 18px;
  font-weight: 500;
}

.notices.yellow {
  background: rgba(239, 171, 76, 0.2);
}
.notices.yellow p {
  border-top: 30px solid rgba(239, 171, 76, 0.8);
}

.notices.red {
  background: rgba(217, 83, 79, 0.2);
}
.notices.red p {
  border-top: 30px solid rgba(217, 83, 79, 0.8);
}

.notices.blue {
  background: rgba(106, 176, 222, 0.2);
}
.notices.blue p {
  border-top: 30px solid rgba(106, 176, 222, 0.8);
}

.notices.green {
  background: rgba(92, 184, 92, 0.2);
}
.notices.green p {
  border-top: 30px solid rgba(92, 184, 92, 0.8);
}